<template>
  <div>
      <div class="goods" v-for="(item, index) in mallarr" :key="index">
        {{ item.title }}
        <div class="goodstight">
          <i class="cubeic-remove" @click="removeMall(index)"></i>
          <span>{{ item.mallCount }}</span>
          <i class="cubeic-add" @click="addMall(index)"></i>
        </div>
      </div>
      <cube-button class="cbbut">下单</cube-button>
      <cube-button class="cbbut" @click="clearMall">清空购物车</cube-button>
  </div>
</template>

<script>
//引入vuex的方法 mapxxx 下面可以直接使用
import { mapState } from 'vuex'
export default {
  data() {
    return {
    }
  },

  computed: {
    //使用方法都是一样的  ...
    ...mapState({
      //把vuex里的arrMall传到mallarr里
      mallarr:state => state.arrMall
    })
  },

  

  methods: {
    removeMall(index) {
      this.$store.commit('removeMall', index)
    },
    addMall(index) {
      this.$store.commit('addMall', index)
    },
    clearMall() {
      this.$store.commit('clearMall')
    }
  },
}
</script>

<style lang="stylus">
  .goods 
    padding 5px 
    text-align left
  .goodstight
    float right
  .cbbut
    margin  5px 0
</style>